* {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.App {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
}

.container {
  box-shadow: 0px 0px 12px #ccc;
  border-radius: 8px;
  padding: 3rem;
  width: 400px;
}


h2 {
  margin-bottom: 30px;
  font-size: 2rem;
}

label {
  display: flex;
}

input {
  width: 100%;
  font-size: 1rem;
  padding: 15px 15px;
  margin: 8px 0px;
  border-radius: 8px;
  border: 1px solid #aab2bd;
}

.btn {
  display: block;
  width: 100%;
  font-size: 1.2rem;
  margin: 8px 0px;
  padding: 15px 0px;
  background-color: #008F8C;
  color: #fff;
  border: 1px solid #333;
  border-radius: 8px;
  cursor: pointer;
}

.btn:hover {
  background-color: #04b4b1;
  color: white;
}

.btn-outline {
  background-color: #dce6f3;
  color: black;
}

.btn-outline:hover {
  background-color: #9fa8b4;
  color: black;
}

.center {
  text-align: center;
  margin: 24px 0 0 0;
}

p {
  color: #8d8b8b;
  margin: 10px 0;
}

.warn {
  color: rgb(247, 139, 100);
  font-weight: 500;
}

